import { STORAGE_KEY } from "./../../../services/constants";
import { ClubProvider } from "./../../../providers/club-provider";
import { StorageService } from "./../../../services/storage-service";
import { CommunityMyOption } from "./../../beta/community-option/community-option";

import { SocialProvider } from "./../../../providers/social/social";

import { SqliteProvider } from "../../../providers/sqlite-provider";
import { PreloadProvider } from "./../../../providers/preload-provider";
import { ServerProvider, CacheProvider } from "./../../../providers/providers";
import { Component, ViewChild, ChangeDetectorRef } from "@angular/core";
import {
  IonicPage,
  NavController,
  Events,
  ModalController,
  Content
} from "ionic-angular";
import { TranslateService } from "@ngx-translate/core";
import { UserProvider } from "../../../providers/user-provider";
import { Session, NativeService, Utility } from "../../../services/services";
import { ConfigProvider, WidgetProvider } from "../../../providers/providers";
import { Webpage } from "../../../models/models";

@IonicPage()
@Component({
  selector: "page-my-list",
  templateUrl: "my-list.html"
})
export class MyListPage {
  @ViewChild(Content) content: Content;
  @ViewChild("header") header;
  @ViewChild("toolbar") toolbar;

  currentUser: any = {
    pictureUrl: "assets/img/avatar-round.png"
  };
  staffNumber: string;
  currentVerionNo: string;
  lastestVerionNo: string;
  needUpdate: boolean = false;
  isLogin: Boolean = false;
  isEmployee: Boolean = false;
  showHeader: boolean = false;
  isBindWeChat: boolean = false;
  isBeta: boolean = true;
  tmpUrl: string = "http://www.popochiu.com/upload/1558145430468.html";
  CommunityUrls: CommunityMyOption = {
    follows: this.tmpUrl,
    publish: this.tmpUrl,
    comments: this.tmpUrl,
    avtivity: this.tmpUrl
  };
  achieveItem: any = {
    score: 0,
    ranking: 0,
    medalQuantity: 0,
    level: "LV1",
    ratio: "0%"
  };
  showMass: boolean = false;
  showSocial: boolean = false;

  constructor(
    public navCtrl: NavController,
    public userProvider: UserProvider,
    public events: Events,
    public session: Session,
    private translate: TranslateService,
    public nativeService: NativeService,
    public configProvider: ConfigProvider,
    public widgetProvider: WidgetProvider,
    private serverProvider: ServerProvider,
    private preload: PreloadProvider,
    public sqliteProvider: SqliteProvider,
    public modalController: ModalController,
    public socialProvider: SocialProvider,
    public storage: StorageService,
    public clubProvider: ClubProvider,
    public cd: ChangeDetectorRef,
    public cacheProvider: CacheProvider
  ) {
    if (this.session.user != null) {
      this.currentUser = this.session.user;
      this.isLogin = true;
    }
    this._InfoConfig();
    this.checkVersion();
    this.getSocialProfile();

    let func = [this.getSocialProfile, this._InfoConfig];
    let eventList = [
      {
        eventName: "user:info-changed",
        func: [0, 1]
      },
      {
        eventName: "user:permission-changed",
        func: [0, 1]
      },
      {
        eventName: "achievement:refresh-assets",
        func: [0]
      },
      {
        eventName: "MassFlagChange",
        func: [1]
      },
      {
        eventName: "changeCommunityUrl",
        func: [1]
      }
    ];

    eventList.forEach(item => {
      this.events.subscribe(item.eventName, () => {
        item.func.forEach(f => {
          func[f].call(this);
        });
      });
    });

    // //當用戶信息變更時，接收廣播
    // this.events.subscribe('user:info-changed', () => {
    //   this.getSocialProfile()
    //   this._InfoConfig()
    // });
    // this.events.subscribe('user:permission-changed', () => {
    //   this.getSocialProfile()
    //   this._InfoConfig()
    // });

    // this.events.subscribe('achievement:refresh-assets', () => {
    //   this.getSocialProfile()
    // });

    // this.events.subscribe('MassFlagChange', () => {
    //   this._InfoConfig()
    // })

    // this.events.subscribe('changeCommunityUrl', () => {
    //   this._InfoConfig();
    // })
  }

  async _InfoConfig() {

    try{
      let res = await this.cacheProvider.getSocialSetting()  
      this.showSocial = res.showSocial
      this.showMass = res.showMass
      this.session.showSocial = res.showSocial
    }catch(e){ } 
 
    this.currentUser = this.session.user;
    this.isEmployee = this.session.isEmployee;
    if (this.currentUser) {
      this.isBindWeChat = this.currentUser.isBindedWX || this.currentUser.nickNameWX || this.currentUser.headImgUrlWX; 
      this.isLogin = true;
      this.currentUser.pictureUrl =
        this.currentUser.pictureUrl || "assets/img/avatar-round.png";
      if (this.currentUser.employeeInfo) {
        this.staffNumber = this.currentUser.employeeInfo.staffNumber;
      }
    } else {
      this.staffNumber = "";
    } 
    let CommunityUrls = await this.storage.getItem("CommunityUrls");
    if (CommunityUrls && CommunityUrls.my) {
      this.CommunityUrls = CommunityUrls.my;
    }
    this.content.scrollToTop();
    this.showHeader = false;
  }
  /**
   * The view loaded, let's query our items for the list
   */
  ionViewDidLoad() {
    if (!this.isLogin) {
      this.userProvider.getUserInfo();
    }
  }

  ionViewWillEnter() {
    this.configProvider.init().then(() => {
      this.checkVersion();
    });
  }
  openMedalPage() {
    this.navCtrl.push("MymodalPage");
  }

  getSocialProfile(_event?) {
    this.socialProvider.getProfiles().subscribe(
      res => {
        _event && _event.complete();
        this.achieveItem = this.socialProvider.socialProfiles;
        if (
          this.achieveItem.currentLevelExperience +
            this.achieveItem.nextLevelExperience >
          0
        ) {
          this.achieveItem.ratio =
            (this.achieveItem.currentLevelExperience * 100) /
              (this.achieveItem.currentLevelExperience +
                this.achieveItem.nextLevelExperience) +
            "%";
        }
      },
      err => {
        _event && _event.complete();
      }
    );
  }

  openScancode() {
    event.stopPropagation();
    this.navCtrl.push("MycodePage");
  }

  openSignPage() {
    this.navCtrl.push("SignInPage");
  }

  openSociality(index) {
    if (index == 1) {
      this.navCtrl.push("MySegmentPage", { socialcontact: "fabiao" });
    } else if (index == 2) {
      this.navCtrl.push("MySegmentPage", { socialcontact: "guanzhu" });
    } else if (index == 3) {
      this.navCtrl.push("MySegmentPage", { socialcontact: "fans" });
    } else {
      this.navCtrl.push("MySegmentPage", { socialcontact: "huozan" });
    }
  }

  

  openMyFanstPage(item) {
    this.clubProvider.openWebpage(this.clubProvider.MyFans(), this.navCtrl);
  }

  openCollectPage(item) {
    this.clubProvider.openWebpage(this.clubProvider.MyFavorite(), this.navCtrl);
  }

  openAccumulatePoints(item) {
    this.navCtrl.push("AccumulatePointsPage");
  }
  openSecurity() {
    if (this.checkLogin()) {
      this.navCtrl.push("SecurityPage");
    }
  }
  /**
   * Navigate to my info page.
   */
  openEmployeeIdentityPage() {
    if (this.checkLogin()) {
      this.navCtrl.push("EmployeeIdentityPage");
    }
  }

  /**
   * Navigate to my info page.
   */
  openMyInfoPage() {
    if (this.checkLogin()) {
      this.navCtrl.push("MyInfoPage");
    }
  }
  /**
   * Navigate to the about page.
   */
  openAboutPage() {
    let params = { language: this.session.language };
    if (this.currentUser) {
      params["isAdmin"] = this.currentUser.isAdmin;
      params["userId"] = this.currentUser.userId;
    }
    this.navCtrl.push("AboutPage", params);
  }

  /**
   * Navigate to settings page.
   */

  /**
   * 分享
   */
  openSharePage() {
    let shareMessage = {
      title: this.translate.instant("SHARE_TITLE"), //标题
      description: this.translate.instant("SHARE_DESCRIPTION"), //描述
      thumb: "www/assets/imgs/logo.png", //缩略图
      resource: "https://iproud.fihmb.com/AppClient" //图片url或网页url
    };

    this.preload.openShareModal({
      shareMessage: shareMessage,
      navigationDockTargetMapIds: [1, 2],
      isShareImage: false,
      qrcode: "assets/imgs/iproud_qrcode.png"
    });
  }

  openSettingsPage() {
    let params = { language: this.session.language };
    if (this.currentUser) {
      params["isAdmin"] = this.currentUser.isAdmin;
      params["userId"] = this.currentUser.userId;
    }
    this.navCtrl.push("SettingsPage", params);
  }

  openService() {
    this.widgetProvider.openFeedback(this.navCtrl);
  }

  //登出
  doLogout() {
    this.isLogin = false;
    this.currentUser = {
      pictureUrl: "assets/img/avatar-round.png",
      employeeInfo: {}
    };
    this.staffNumber = "";
    this.userProvider.logout().subscribe(() => {
      this.events.publish("user:logout");
      this.sqliteProvider.closeUserDataBase();
    });
  }

  //是否已經登錄，若沒登錄，則打開登錄頁面
  checkLogin() {
    if (!this.isLogin) {
      this.events.publish("user:reLogin");
      return false;
    }
    return true;
  }

  checkVersion() {
    this.currentVerionNo = this.configProvider.currentVersionNo;
    this.lastestVerionNo = this.configProvider.latestVersionNo;
    this.needUpdate = Utility.checkNewVersion(
      this.lastestVerionNo,
      this.currentVerionNo
    );
  }

  openCommunityPage(url) {
    let page: Webpage = {
      url: url,
      options: {
        useNativeBrowser: "iframe"
      }
    };
    this.widgetProvider.openWebpage(page, this.navCtrl);
  }
  /** 我的评论 */
  openMyComment() {
    this.clubProvider.openWebpage(this.clubProvider.MyComment(), this.navCtrl);
  }

  /**
   * 我的关注
   */
  openMyFollows() {
    this.clubProvider.openWebpage(this.clubProvider.MyFollow(), this.navCtrl);
  }

  /**
   * 我的活动
   */
  openMyEvents() {
    this.clubProvider.openWebpage(this.clubProvider.MyActivity(), this.navCtrl);
  }

  /**
   * 我的发表
   */
  openMyPublish() {
    this.clubProvider.openWebpage(this.clubProvider.MyPublish(), this.navCtrl);
  }

  openSpeechPage() {
    this.navCtrl.push("SpeechPage");
  }

  /**
   * 任务清单
   */
  openTaskList() {
    this.navCtrl.push("AchieveTaskListPage");
  }

  /**
   * 我的成就
   */
  openAchievement() {
    this.navCtrl.push("AchievementPage");
  }

  /**
   * 我的员宝贝
   */
  openMyAssets(selected?: string) {
    this.navCtrl.push("MyAssetsPage", {
      selected: selected,
      details: this.achieveItem
    });
  }
  /**
   * 制度说明
   */
  openTerms() {
    this.modalController
      .create("PrivacyPolicyPage", {
        subjectId: 74605615803392,
        language: this.session.language
      })
      .present();
  }

  checkExprience() {
    this.nativeService.showToast(`
    ${this.translate.instant("MY_LIST_EXPERIENCE")}${
      this.achieveItem.experience
    }
    ${this.translate.instant("MY_LIST_NEXT_LEVEL_REQUIRES")} 
    ${this.achieveItem.nextLevelExperience}`);
  }

  doRefresh(_event) {
    this.getSocialProfile(_event);
  }

  scrollEvent(e) {
    e && (() =>{
      let scrollTop = e.scrollTop;
      if (scrollTop > 35) {
        this.showHeader = true;
      } else {
        this.showHeader = false;
      }
  
      this.cd.detectChanges();
    })() 
  }
}



